/*------------------------------------*\
    Typography
\*------------------------------------*/

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
    margin: 0 0 1.5rem;
}

h1,
.h1 {
    @include f-xl();
}

h2,
.h2 {
    @include f-lg();
}

h3,
.h3 {
    @include f-md();
}

// h4,
